"use server"
import React from "react";
import Link from "next/link";
import { db } from "~/server/db";
import { montserrat, pacifico } from "~/lib/font";
import { redirect } from "next/navigation";
import { auth } from "~/server/auth";
import Pagination from "~/app/ui/pagination";
import { revalidatePath } from "next/cache";

export async function ProductList(searchParams?: {
  size?: string;
  page?: string;
  userId?: string;
}) {
  const count = await db.user.count();
  const session = await auth();

  // Если пользователь не авторизован
  if (!session?.user?.email) {
    redirect("/api/auth/signin");
  }

  // Получаем пользователя из базы по email
  const user = await db.user.findUnique({
    where: {
      email: session.user.email,
    },
    select: {
      id: true,
      name: true,
      surname: true,
    },
  });

  // Редирект, если нет имени и фамилии
  if (!user?.name || !user?.surname) {
    redirect(`/profile/${user?.id}`);
  }
  // Пагинация
  const page = Number(searchParams?.page ?? "1");
  const size = Number(searchParams?.size ?? "16");
  const pages = Math.ceil(count / size);

  // Получение товаров
  const products = await db.product.findMany({
    include: {
      images: true,
    },
  });

  if (products.length === 0) {
    return (
      <p className={`text-center text-gray-500 ${montserrat.className}`}>
        Нет доступных товаров
      </p>
    );
  }
  {products.map((product) => {
    const hasImage = product.images[0]?.url;
    const imageUrl = hasImage || "/placeholder-product.jpg";
  
    return (
      <Link
        key={product.id}
        href={`/product/${product.id}`}
        className={`rounded-xl border p-5 shadow-sm transition-all duration-300 block hover:shadow-lg ${
          hasImage ? "hover:border-purple-200 border-gray-200" : "border-gray-300 bg-gray-100 opacity-80"
        }`}
      >
        <div className="aspect-square relative w-full mb-4 rounded-lg overflow-hidden bg-gray-50">
          <img
            src={imageUrl}
            alt={product.name}
            className="w-full h-full object-cover transition-transform duration-500 hover:scale-105"
          />
        </div>
        <h2 className="text-xl font-bold line-clamp-2 hover:text-purple-600 transition-colors font-serif">
          {product.name}
        </h2>
        <p className="text-sm text-gray-600 line-clamp-3 mb-3 mt-2 italic">
          {product.description}
        </p>
        <p className={`font-bold text-purple-600 text-lg ${pacifico.className}`}>
          {product.price.toFixed(2)} ₽
        </p>
      </Link>
    );
  })}
}  